html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  background: #fff;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "SegoeUI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "HelveticaNeue", sans-serif;
}
html *,
body * {
  box-sizing: border-box;
}
html #root,
body #root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
html #root.mobile,
body #root.mobile {
  font-size: 0.7rem;
}
html #root.mobile .navigation nav,
body #root.mobile .navigation nav {
  justify-content: start;
  max-height: fit-content;
}
html #root.mobile .navigation nav .logo,
body #root.mobile .navigation nav .logo {
  font-size: 0.8em;
}
html #root.mobile .navigation nav ul,
body #root.mobile .navigation nav ul,
html #root.mobile .navigation nav .sign-in,
body #root.mobile .navigation nav .sign-in {
  display: none;
}
html #root.mobile .navigation nav ul,
body #root.mobile .navigation nav ul {
  width: 100%;
  position: absolute;
  background: #242424;
  top: 50px;
  left: 0;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
}
html #root.mobile .navigation nav ul li a,
body #root.mobile .navigation nav ul li a {
  border-bottom: 1px solid #eee;
}
html #root.mobile section,
body #root.mobile section {
  min-height: calc(100vh - 100px);
}
html #root.mobile section .section-link,
body #root.mobile section .section-link {
  text-decoration: underline;
}
html #root.mobile .events-container,
body #root.mobile .events-container {
  flex-wrap: nowrap;
  position: relative;
  display: contents;
}
html #root.mobile .events-container .controls,
body #root.mobile .events-container .controls {
  height: 100%;
  width: 2em;
  font-size: 2em;
  color: red;
  position: absolute;
  align-items: center;
  display: flex;
  justify-content: center;
  z-index: 1;
}
html #root.mobile .events-container .controls.left,
body #root.mobile .events-container .controls.left {
  left: 0;
}
html #root.mobile .events-container .controls.right,
body #root.mobile .events-container .controls.right {
  right: 0;
}
html .navigation,
body .navigation {
  width: 100%;
  height: 100%;
  background: #242424;
  min-height: 50px;
  color: gold;
  height: fit-content;
  position: sticky;
  top: 0;
  position: relative;
  transition: height 0.3s ease-in-out, opactiy 0.3s ease-in-out;
  z-index: 1;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
}
html .navigation nav,
body .navigation nav {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
html .navigation nav .menu-button,
body .navigation nav .menu-button {
  background: inherit;
  height: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
  aspect-ratio: 1;
}
html .navigation nav .menu-button:hover,
body .navigation nav .menu-button:hover {
  cursor: pointer;
  background-color: #333333;
}
html .navigation nav .logo,
body .navigation nav .logo {
  display: flex;
  filter: brightness(90%);
  padding-left: 1em;
}
html .navigation nav .logo:focus,
body .navigation nav .logo:focus,
html .navigation nav .logo:hover,
body .navigation nav .logo:hover {
  filter: brightness(120%);
}
html .navigation nav .logo h1,
body .navigation nav .logo h1 {
  color: gold;
}
html .navigation nav .logo h2,
body .navigation nav .logo h2 {
  color: #fff;
}
html .navigation nav ul,
body .navigation nav ul {
  display: flex;
}
html .navigation nav ul li a,
body .navigation nav ul li a {
  padding: 1em;
  color: inherit;
  background-color: inherit;
  filter: brightness(65%);
  display: flex;
  align-items: center;
  gap: 0.5em;
}
html .navigation nav ul li a:focus,
body .navigation nav ul li a:focus,
html .navigation nav ul li a:hover,
body .navigation nav ul li a:hover,
html .navigation nav ul li a.active,
body .navigation nav ul li a.active {
  filter: brightness(90%);
}
html .navigation nav ul li a.active:focus,
body .navigation nav ul li a.active:focus,
html .navigation nav ul li a.active:hover,
body .navigation nav ul li a.active:hover {
  filter: brightness(100%);
}
html .navigation nav .sign-in,
body .navigation nav .sign-in {
  display: flex;
  gap: 0.5rem;
  padding-right: 1em;
}
.list-links {
  color: inherit;
  text-decoration: none;
}
a {
  color: inherit;
  text-decoration: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
ul#PasswordChecklist {
  padding: 1em 0.5em;
  list-style: disc;
}
ul#PasswordChecklist li {
  display: flex;
  align-items: center;
}
ul li a {
  color: inherit;
  text-decoration: none;
}
.btn {
  outline: none;
  color: #fff;
  border-radius: 0.3rem;
  padding: 0.7em 1em;
  background-color: inherit;
  transition: all 0.1s ease-in-out;
  filter: brightness(95%);
}
.btn.darken {
  filter: brightness(70%);
}
.btn[disabled] {
  opacity: 0.6;
}
.btn:focus:enabled,
.btn:hover:enabled {
  margin: 0;
  cursor: pointer;
  filter: brightness(100%);
}
.btn.btn-default {
  background-color: #333333;
}
.btn.btn-primary {
  background-color: gold;
  border: 0;
}
.btn.btn-secondary {
  border-color: gold;
}
button,
a.btn {
  font-weight: 600;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
.flex {
  display: flex !important;
}
.container {
  overflow-y: auto;
  overflow-x: hidden;
  height: fit-content;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  z-index: 0;
}
@supports (scroll-snap-type: y mandatory) {
  .container .no-support {
    display: none;
  }
}
.container section {
  min-height: calc(100vh - 70px);
  height: fit-content;
  width: 100%;
  scroll-snap-align: start;
  scroll-snap-stop: normal;
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  padding: 1em;
}
.container section:nth-child(odd) {
  background: #eee;
}
.container section .section-title {
  text-align: center;
  padding-bottom: 0.5em;
}
.container section .section-title .section-link:hover,
.container section .section-title .section-link:focus {
  text-decoration: underline;
}
#Login,
#SignUp {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.1);
}
#Login form,
#SignUp form {
  width: 100%;
  padding: 2em 0;
}
#Login form .btn,
#SignUp form .btn {
  margin-top: 1em;
}
.card {
  width: 400px;
  max-width: 90%;
  height: fit-content;
  max-height: 90%;
  padding: 1em;
  background: #fff;
  color: #000;
  border-radius: 0.5rem;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card .card-title {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}
.card .card-title svg.close {
  opacity: 0.6;
}
.card .card-title svg.close:hover,
.card .card-title svg.close:focus {
  cursor: pointer;
  opacity: 1;
}
.card a {
  text-decoration: underline;
  color: gold;
}
.form-group {
  margin-bottom: 0.5em;
}
input.form-control {
  border: 1px solid gold;
}
.form-control {
  border-radius: 2em;
  padding: 0.8em 1em;
  outline: 0;
  border: 1px solid #eee;
}
.form-control.wide {
  width: 100%;
}
.form-control.invalid {
  border: 1px solid red !important;
}
.form-control.valid {
  border: 1px solid green !important;
}
.text-danger {
  color: red;
}
.passwordStrengthBar {
  height: 1px;
  padding: 0.5em 0;
}
.passwordStrengthBar .bars {
  display: flex;
  gap: 0.3em;
  justify-content: space-evenly;
}
.passwordStrengthBar .bars .bar {
  border: 1px solid #ddd;
  width: 100%;
}
.passwordStrengthBar .bars .bar.weak {
  border: 1px solid red !important;
}
.passwordStrengthBar .bars .bar.ok {
  border: 1px solid orange !important;
}
.passwordStrengthBar .bars .bar.good {
  border: 1px solid yellow !important;
}
.passwordStrengthBar .bars .bar.strong {
  border: 1px solid green !important;
}
.passwordStrengthBar small {
  float: right;
  padding: 0.5em;
}
.welcome-container {
  display: flex;
}
.gallery-container .gallery-card .description {
  padding-bottom: 0;
}
.events-container,
.gallery-container {
  display: flex;
  gap: 2em;
  justify-content: center;
  flex-wrap: wrap;
  order: 5;
}
.events-container .add-card,
.gallery-container .add-card {
  aspect-ratio: 1;
  height: 90%;
  scale: 0.95;
}
.events-container .event-card,
.gallery-container .event-card {
  padding: 0;
  background: #242424;
  color: gold;
  overflow: hidden;
  filter: brightness(70%);
  box-shadow: unset;
  scale: 0.95;
  transition: scale 0.1s ease-in-out;
  justify-content: stretch;
  max-height: 100%;
}
.events-container .event-card:hover,
.gallery-container .event-card:hover,
.events-container .event-card:focus,
.gallery-container .event-card:focus {
  cursor: pointer;
  filter: brightness(100%);
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
  scale: 1;
}
.events-container .event-card .description,
.gallery-container .event-card .description {
  width: 100%;
  padding: 1em;
  text-align: left;
  height: fit-content;
}
.events-container .event-card .description h5,
.gallery-container .event-card .description h5 {
  display: flex;
  justify-content: space-between;
}
.events-container .event-card .description p,
.gallery-container .event-card .description p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: inherit;
  transition: height 0.5s ease-in-out;
}
.events-container .event-card .thumbnail,
.gallery-container .event-card .thumbnail {
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  aspect-ratio: 5 / 3;
}
.events-container .event-card .read-more,
.gallery-container .event-card .read-more {
  margin-top: 1em;
  color: #fff;
  float: right;
}
.theme-text-color {
  color: gold !important;
}
